.main {
  position: relative;
  &__content {
    display: grid;
    row-gap: 92px;
    padding-block-start: 60px;
    padding-block-end: 120px;

    @include onTablet {
      row-gap: 120px;
      padding-block: 120px;
    }

    @include onDesktop {
      row-gap: 144px;
      padding-block-end: 180px;
    }
  }
}
